.in-box {
    width: 1200px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    // padding: 0 259px;
    .nav-item {
        color: #fff;
        margin: 0;
        height: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor:pointer;
    }
    span {
        display: inline-block;
        height: 2px;
        width: 100%;
        margin-top: -10px;
        background: #fff;
    }
}
 