@charset "UTF-8";
.in-case-box {
  display: flex;
  flex-direction: column;
  width: 1200px;
  margin: auto; }

.title-box {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 58px; }
  .title-box .title {
    text-align: center;
    font-size: 30px;
    color: #333333;
    font-weight: bold; }
  .title-box span {
    height: 2px;
    width: 100px;
    display: inline-block;
    margin: auto;
    background: #333333; }

.seo-case-list {
  width: 900px;
  margin: auto;
  margin-top: 70px; }
  .seo-case-list .seo-case-first {
    display: flex;
    flex-direction: row;
    margin-bottom: 60px; }
    .seo-case-list .seo-case-first img {
      width: 310px;
      height: 230px; }
    .seo-case-list .seo-case-first .first-msg {
      margin-left: 50px; }
      .seo-case-list .seo-case-first .first-msg .first-time {
        font-size: 30px;
        font-family: MicrosoftYaHei-Bold;
        font-weight: bold;
        color: #008BE4;
        margin: 0; }
      .seo-case-list .seo-case-first .first-msg .first-name {
        font-size: 30px;
        font-family: MicrosoftYaHei-Bold;
        font-weight: bold;
        color: #333;
        margin-top: 10px; }
      .seo-case-list .seo-case-first .first-msg .first-keywords {
        font-size: 20px;
        margin-top: 5px;
        font-family: MicrosoftYaHei-Bold;
        font-weight: bold;
        color: #333;
        display: flex;
        flex-direction: row;
        align-items: center; }
        .seo-case-list .seo-case-first .first-msg .first-keywords .keywords-box span {
          margin-left: 20px; }
      .seo-case-list .seo-case-first .first-msg .first-desc {
        margin-top: 5px;
        font-size: 18px;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: #333; }
      .seo-case-list .seo-case-first .first-msg .see-more {
        margin-top: 5px;
        width: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 7px 0;
        background: #008BE4;
        border-radius: 17px;
        color: #fff;
        font-size: 10px; }
  .seo-case-list .seo-case-item {
    margin-top: 20px;
    padding: 25px;
    display: flex;
    flex-direction: row;
    border: 1px dotted #9c9c9c;
    border-radius: 8px; }
    .seo-case-list .seo-case-item:hover {
      box-shadow: #d6d6d6 0 0 10px;
      border: none;
      cursor: pointer; }
    .seo-case-list .seo-case-item .seo-case-img {
      width: 205px;
      height: 124px; }
      .seo-case-list .seo-case-item .seo-case-img img {
        width: 100%;
        height: 100%; }
    .seo-case-list .seo-case-item .item-msg {
      margin-left: 20px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%; }
      .seo-case-list .seo-case-item .item-msg .item-desc-msg {
        display: flex;
        flex-direction: column; }
        .seo-case-list .seo-case-item .item-msg .item-desc-msg .item-name {
          font-size: 24px;
          font-family: MicrosoftYaHei-Bold;
          font-weight: bold;
          color: #333; }
        .seo-case-list .seo-case-item .item-msg .item-desc-msg .item-keywords {
          font-size: 16px;
          font-family: MicrosoftYaHei-Bold;
          font-weight: bold;
          color: #333;
          display: flex;
          flex-direction: row;
          align-items: center; }
          .seo-case-list .seo-case-item .item-msg .item-desc-msg .item-keywords .keywords-item {
            margin-left: 20px; }
        .seo-case-list .seo-case-item .item-msg .item-desc-msg .item-desc {
          margin-top: 20px;
          font-size: 16px;
          font-family: MicrosoftYaHei;
          font-weight: 400;
          color: #333; }
    .seo-case-list .seo-case-item .item-time {
      display: flex;
      flex-direction: column;
      font-family: '微软雅黑'; }
      .seo-case-list .seo-case-item .item-time .time-day {
        font-size: 60px;
        color: #333;
        margin: 0; }
      .seo-case-list .seo-case-item .item-time .item-mouth {
        font-size: 14px;
        color: #333; }

.pagin-box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0; }
